import STATE_core from '@/store/STATE_core.js';
import STATE_params from '@/store/STATE_params.js';
import Cookies from '@/logic/Cookies.js';
import Functions from '@/logic/Functions.js';

/**
 * Cookie_manager
*/
class Cookie_manager
{
	/**
	 * init_manager
	*/
	init_manager()
	{
		if (this.exit_manager()) {
			return;
		}

		// show group manager if group wasnt picked
		if (STATE_core().product_group_id() === -1) {
			STATE_core().change_step(111);
			return;
		}

		// retrieve session from cookies if it exists
		if (STATE_core().is_fresh_session()) {
			let cookie = Cookies.get_cookie(this.c_name());
			if (cookie?.session) {
				STATE_core().set_session_token(cookie.session);
			}
		}

		// retrieve step from cookies if it exists, default to 1
		if (STATE_core().current_step() === -1) {
			let cookie = Cookies.get_cookie(this.c_name());
			if (cookie?.step) {
				STATE_core().change_step(cookie.step);
			} else {
				STATE_core().change_step(1);
			}
		}
	}


	/**
	 * update_cookies
	*/
	async update_cookies()
	{
		if (this.exit_manager()) {
			return;
		}

		let session = STATE_core().get_session_token();
		let step = STATE_core().current_step();

		// dont update for session manager or cleared session
		if (step === -1 || session === '') {
			return;
		}

		// wait a second so receiver from call_params is set
		await Functions.sleep(1); // 1s

		let cookie = {
			session: session,
			expire: null,
			step: step,
			receiver: STATE_params().get_receiver(),
			endpoint: STATE_core().get_endpoint_type(),
		};

		Cookies.set_cookie(this.c_name(), cookie, 14);
	}


	/**
	 * purge_cookies
	*/
	purge_cookies(id = undefined)
	{
		if (this.exit_manager()) {
			return;
		}
		Cookies.delete_cookie(this.c_name(id));
	}


	/**
	 * retrieve_cookies
	*/
	retrieve_cookies(id/*required this time*/, name)
	{
		if (this.exit_manager()) {
			return;
		}

		let cookie = Cookies.get_cookie(this.c_name(id));
		let step = cookie?.step || 1;
		let session = cookie?.session || '';
		let receiver = cookie?.receiver || '';
		let endpoint = cookie?.endpoint || '';
		let expire = cookie?.expire || '';
		if (expire) {
			expire = new Date(expire);
			expire = expire.toLocaleString('sk-SK');
		}

		return {
			name: name,
			step: step,
			session: session,
			expire: expire,
			receiver: receiver,
			endpoint: endpoint,
		};
	}


	/**
	 * exit_manager
	*/
	exit_manager()
	{
		// do nothing if manager is off
		return !STATE_core().is_cookie_manager_on() || STATE_core().get_run_mode() !== 'form_app';
	}


	/**
	 * c_name
	*/
	c_name(id = undefined)
	{
		// f.e.: osmc_2_edu_7
		return 'osmc_' + STATE_params().get_receiver() + '_' + STATE_core().get_endpoint_type() + '_' + (id || STATE_core().product_group_id());
	}
}

export default new Cookie_manager;
