import { createApp } from 'vue'
import App from './App.vue'
import { createPinia } from 'pinia'
const pinia = createPinia()

import Form_cache from './logic/Form_cache.js'

import STATE_core from './store/STATE_core.js'
import STATE_docs_landing from './store/STATE_docs_landing.js'
import STATE_error from './store/STATE_error.js'
import STATE_flow from './store/STATE_flow.js'
import STATE_forms from './store/STATE_forms.js'
import STATE_modals from './store/STATE_modals.js'
import STATE_nav from './store/STATE_nav.js'
import STATE_params from './store/STATE_params.js'
import STATE_request from './store/STATE_request.js'

class Osm_app
{
	attempt = 0;
	max = 10;
	app = null;

	/**
	 * init
	*/
	init(config)
	{
		// return promise so myport knows when init is done
		return new Promise(
			(resolve, reject) => {

				// exit if required document selector is missing
				if (document.getElementById('osm_app') === null) {
					return reject('missing #osm_app');
				}

				// remove placeholder html from <div id="osm_app"></div>
				Form_cache.clear_form_cache();

				// initialize app if not running
				if (this.app === null) {

					// init
					this.app = createApp(App).use(pinia);
					this.app.provide('config', config).mount('#osm_app');

					// mount() seem to be synchronous, wait anyway and inform myport
					return setTimeout(
						() => {
							// on success reset attempt
							this.attempt = 0;
							return resolve('init success');
						},
						200 // 0.2s
					);
				}

				// in case its already running, be sure to destroy it first, myport had problems with that
				this.destroy();

				// and initialize after small delay
				return setTimeout(
					() => {
						// just a fallback if it got stuck
						if (this.attempt > this.max) {
							this.attempt = 0;
							return reject('osmapp init failed');
						}

						// repeat whole method
						this.attempt++;
						return this.init(config);
					},
					200 // 0.2s
				);

			}
		); // Promise END
	}

	/**
	 * destroy
	*/
	destroy()
	{
		// exit if requested accidentally
		if (this.app === null) {
			return;
		}

		// reset pinia state
		STATE_core().$reset();
		STATE_docs_landing().$reset();
		STATE_error().$reset();
		STATE_flow().$reset();
		STATE_forms().$reset();
		STATE_modals().$reset();
		STATE_nav().$reset();
		STATE_params().$reset();
		STATE_request().abort_all_signals();

		// kill vue
		this.app.unmount();
		this.app = null;
	}

	/**
	 * dump
	*/
	dump()
	{
		// exit if requested accidentally
		if (this.app === null) {
			return;
		}
		// structuredClone cant process save_exposed_state if its value is function
		let dump = pinia.state.value;
		dump.STATE_core.save_exposed_state = typeof dump.STATE_core.save_exposed_state;
		dump.STATE_core.get_access_token_promise = typeof dump.STATE_core.get_access_token_promise;
		dump.STATE_core.get_fresh_session_token_promise = typeof dump.STATE_core.get_fresh_session_token_promise;
		dump.STATE_request.abort_controller = typeof dump.STATE_request.abort_controller;
		return structuredClone(dump);
	}

	/**
	 * autofill
	*/
	autofill_run()
	{
		// exit if requested accidentally
		if (this.app === null) {
			return;
		}

		// seed the form
		STATE_params().autofill_run();
	}
}

window.osm_app = new Osm_app;
