<template>
	<Modal_body :close="true" title="Zotavenie session" class="modal-error" >

		<p>
			Skontrolujeme ak bol posledn spen stav porovnania.
		</p>

		<div class="text-center p-3">
			<Bouncer_loading v-if="designated_location === 0" size="large" />
			<button
				v-else
				@click="send_troops"
				class="btn btn-secondary"
			>
				Pokracova v kroku {{ this.designated_location }}
			</button>
		</div>

	</Modal_body>
</template>

<script>
	import Modal_body from '@/components/modals/parts/Modal_body.vue';
	import Session_data from '@/logic/requests/Session_data.js';
	import STATE_core from '@/store/STATE_core.js';
	import STATE_modals from '@/store/STATE_modals.js';
	import Forms_group from '@/logic/requests/Forms_group.js';
	import Forms_user from '@/logic/requests/Forms_user.js';
	import STATE_params from '@/store/STATE_params.js';
	import Bouncer_loading from '@/components/global/Bouncer_loading.vue';

	export default {
		props: {
		},
		components: {
			Modal_body,
			Bouncer_loading,
		},
		emits: [],
		data() {
			return {
				designated_location: 0,
			}
		},
		created() {
			Promise.resolve().then(
				() => {
					console.log('getsessioninfo');
					return Session_data.session_info();
				}
			).then(
				(response) => {
					console.log('stav session je: ' + response.result.state);
					if (response.result?.state >= 100) {
						console.log('session je uz confirmuta, takze do step5');
						this.designated_location = 5;
						return Promise.reject('resurrection done');
					}
				}
			).then(
				() => {
					console.log('validategroupform');
					return Forms_group.validate_group_form();
				}
			).then(
				(response) => {
					console.log('je form validny? ' + response.result.validform);
					if (response.result.validform === false) {
						console.log('neni. takze pojdes do step1');
						this.designated_location = 1;
						return Promise.reject('resurrection done');
					}
				}
			).then(
				() => {
					console.log('getdata');
					return Session_data.get_data();
				}
			).then(
				(response) => {
					console.log('bol vybraty produkt id: ' + response.result?.call_params?.product_id);
					STATE_params().extract_selected_product(response);
					if (STATE_params().get_selected_product().product_id === null) {
						console.log('takze nebol. pojdes step2.');
						this.designated_location = 2;
						return Promise.reject('resurrection done');
					}
				}
			).then(
				() => {
					console.log('validateform');
					return Forms_user.validate_user_form();
				}
			).then(
				(response) => {
					console.log('je formular validny? ' + response.result.validform);
					if (response.result.validform === false) {
						console.log('neni. takze sup do step3');
						this.designated_location = 3;
						return Promise.reject('resurrection done');
					}
				}
			).then(
				() => {
					console.log('getsessioninfo');
					return Session_data.session_info();
				}
			).then(
				(response) => {
					console.log('stav session je: ' + response.result.state);
					if (response.result?.state < 100) {
						console.log('este neni confirmuta, takze do step4');
						this.designated_location = 4;
						return Promise.reject('resurrection done');
					}
				}
			).then(
				() => {
					console.log('uz je confirmuta, tak sup step5');
					this.designated_location = 5;
				}
			).catch((error) => { console.log(error); });
		},
		methods: {
			send_troops()
			{
				STATE_core().change_step(this.designated_location);
				STATE_modals().close_them_all();
			},
		},
		computed: {
		},
		watch: {
		},
	}
</script>
