import { defineStore } from 'pinia';

export default defineStore(
	'STATE_request',
	{
		state: () => {
			return {
				// null by default
				abort_controller: null,
			};
		},
		getters: {
			get_abort_signal()
			{
				return () => {
					// create controller for this view
					if (this.abort_controller === null) {
						this.abort_controller = new AbortController();
					}
					// all requests in current view share one controller and one signal
					return this.abort_controller.signal;
				}
			},
		},
		actions: {
			abort_all_signals()
			{
				// fallback, should not happen
				if (this.abort_controller === null) {
					return;
				}
				// abort all running requests
				this.abort_controller.abort();
				// reset to null, has to be sure it wont abort any further fetch
				this.abort_controller = null;
			},
		},
	}
);
