<template>
	<section class="session_cookie_manager">
		<table class="table keep_table_styles table-striped table-light table-hover table-borderless align-middle table-sm">
			<thead>
				<tr>
					<th scope="col">ID</th>
					<th scope="col">name</th>
					<th scope="col">step</th>
					<th scope="col">session</th>
					<th scope="col">expire</th>
					<th scope="col">endpoint</th>
					<th scope="col">receiver</th>
					<th scope="col">open</th>
					<th scope="col">delete</th>
				</tr>
			</thead>
			<tbody>
				<tr
					v-for="(group, id) in all_data"
				>
					<th scope="row">{{ id }}</th>
					<td>{{ group.name }}</td>
					<td>{{ group.step }}</td>
					<td>{{ group.session }}</td>
					<td>{{ group.expire }}</td>
					<td>{{ group.endpoint }}</td>
					<td>{{ group.receiver }}</td>
					<td>
						<button
							@click="open_this_group(id)"
							class="btn btn-sm btn-primary"
						>
							open
						</button>
					</td>
					<td>
						<button
							@click="clear_this_group(id)"
							class="btn btn-sm btn-secondary"
						>
							clear
						</button>
					</td>
				</tr>
			</tbody>
		</table>
	</section>
</template>

<script>
	import Constants from '@/logic/Constants.js';
	import Cookies from '@/logic/Cookies.js';
	import STATE_core from '@/store/STATE_core.js';
	import Cookie_manager from '@/logic/Cookie_manager.js';

	export default {
		props: {
		},
		components: {
		},
		emits: [],
		data() {
			return {
				all_data: {
					// 1: {
					// 	name: '',
					// 	step: '',
					// 	token: '',
					// 	expiration: '',
					// },
					// ...
				},
			}
		},
		created() {
			let product_groups = Constants.group_names();

			for (let id in product_groups)
			{
				this.all_data[id] = Cookie_manager.retrieve_cookies(id, product_groups[id]);
			}
		},
		methods: {
			open_this_group(id)
			{
				STATE_core().set_session_token(this.all_data[id].session);
				STATE_core().change_product_group_id(id);
				STATE_core().change_step(this.all_data[id].step);
			},
			clear_this_group(id)
			{
				Cookie_manager.purge_cookies(id);
				this.all_data[id].step = 1;
				this.all_data[id].session = '';
				this.all_data[id].expire = '';
			},
		},
		computed: {
		},
		watch: {
		},
	}
</script>

<style lang="scss">
	.session_cookie_manager {
		button {
			padding: 4px 12px;
		}
	}
</style>
